import { SW } from "./serviceWorker";
const pickFolder = async () => {
    const folderHandle = await window["showDirectoryPicker"]();
    return folderHandle;
};
const init = async () => {
    const folderHandle = await pickFolder();
    await SW.waitForReady();
    SW.post({
        type: "host-start",
    });
    return folderHandle;
};
export const FolderHandle = {
    init,
};
